import React from 'react';
import classNames from 'classnames';
import { Todo } from '../../types/Todo';

interface Props {
  todos: Todo[],
  onShowInfo: (id: number, todo: Todo) => Promise<void>,
  selectedTodo: Todo | null,
}

export const TodoList: React.FC<Props> = ({
  todos,
  onShowInfo,
  selectedTodo,
}) => (
  <table className="table is-narrow is-fullwidth">
    <thead>
      <tr>
        <th>#</th>
        <th>
          <span className="icon">
            <i className="fas fa-check" />
          </span>
        </th>
        <th>Title</th>
        <th> </th>
      </tr>
    </thead>

    <tbody>
      {todos.map(todo => {
        const isSelectedTodo = todo.id === selectedTodo?.id;

        return (
          <tr
            data-cy="todo"
            className={classNames({
              'has-background-info-light': isSelectedTodo,
            })}
            key={todo.id}
          >
            <td className="is-vcentered">{todo.id}</td>

            {todo.completed
              ? (
                <td className="is-vcentered">
                  <span className="icon" data-cy="iconCompleted">
                    <i className="fas fa-check" />
                  </span>
                </td>
              )
              : <td className="is-vcentered" />}

            <td className="is-vcentered is-expanded">
              <p className={classNames('has-text-success', {
                'has-text-danger': !todo.completed,
              })}
              >
                {todo.title}
              </p>
            </td>

            <td className="has-text-right is-vcentered">
              <button
                data-cy="selectButton"
                className="button"
                type="button"
                onClick={() => onShowInfo(todo.userId, todo)}
              >
                <span className="icon">
                  <i className={classNames('far', {
                    'fa-eye-slash': isSelectedTodo,
                    'fa-eye': !isSelectedTodo,
                  })}
                  />
                </span>
              </button>
            </td>
          </tr>
        );
      })}
    </tbody>
  </table>
);
